.Nav-bar{
    width: auto;
    height: 10%;
    min-height: 70px;
    display: flex;
    gap: 8px; /*자식요소간의 간격 */
    align-items: center;
    background-color: #FA5C81;
}
.header_right{
    height: auto;
    width: auto;
    margin-left: 72%;
}

.neon{
    margin-left: 0.5%;
    color: #f8f2f2;
}

@media (max-width: 390px) {
    .neon{
        font-size: 18px;
    }
    .header_right{
        width: 28%;
    }
    .header_button{
        width: 65px;
        height: 20px;
        font-size: 12px;
    }

}